html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  color: #ffffff;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.4) 100%), url("../../build/img/marvel-universe.webp");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

p {
  font-size: 2rem;
  font-family: captain;
}

p span {
  display: block;
  font-size: 80%;
}

[class*=container],
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class*=__heading] {
  color: #284b63;
}

img {
  max-width: 100%;
  display: block;
  pointer-events: none;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 3rem 0;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

@font-face {
  font-family: widow;
  src: url("../../build/fonts/black-widow.ttf");
}
@font-face {
  font-family: captain;
  src: url("../../build/fonts/american-captain.otf");
}
@font-face {
  font-family: avengers;
  src: url("../../build/fonts/avengeance-bold.ttf");
}
h1, h2 {
  font-size: 3rem;
  font-family: captain;
  font-weight: 400;
}

h3 {
  font-size: 2.5rem;
  font-family: avengers;
  font-weight: 400;
}

h4 {
  font-size: 2.5rem;
  font-family: widow;
  font-weight: 400;
}

[class$=heading] {
  margin-bottom: 2rem;
}

a {
  text-decoration: none;
}

.text-white {
  color: white;
}

.mt-3 {
  margin-top: 3rem;
}

.mb-3 {
  margin-bottom: 3rem;
}

.p-block-3 {
  padding: 3rem;
}

.pb-3 {
  padding-bottom: 3rem;
}

.text-center {
  text-align: center;
}

.d-flex {
  display: flex;
}

.d-none {
  display: none;
}

.col-gap-3 {
  column-gap: 3rem;
}

.col-gap-5 {
  column-gap: 5rem;
}

.row-gap-3 {
  row-gap: 3rem;
}

.flex-column {
  flex-direction: column;
}

.align-items-center {
  align-items: center;
}

.justify-content-center {
  justify-content: center;
}

.w-100 {
  width: 100%;
}

.w-40 {
  width: 40%;
}

.min-20 {
  min-width: 20rem;
}

/** Media Queries **/
.header {
  position: sticky;
  top: 0;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 2;
}

.swiper-decks {
  height: auto;
  width: 250px;
  margin: auto;
}

.swiper-decks .swiper-slide {
  cursor: grab;
}

.swiper-decks .swiper-slide img {
  margin: 0 auto;
}

.swiper-cards.cards {
  width: 100%;
  height: 60rem;
  overflow: hidden;
}

.swiper-cards.cards .swiper-slide {
  background-color: rgba(216, 212, 212, 0.4);
  border-radius: 2rem;
  width: 10rem;
  height: 28rem;
  cursor: grab;
}

.swiper-cards.cards .swiper-slide h3 {
  padding-block: 1rem;
}

.swiper-cards.cards .swiper-slide p {
  margin: 0;
  padding-inline: 1.5rem;
}

.swiper-cards.cards .swiper-slide img {
  margin: 0 auto;
}

.info {
  display: flex;
  flex-direction: column;
}

.info p {
  margin: 0;
}

.info p span {
  display: inline;
  padding-left: 1.5rem;
}

.info:nth-child(3) {
  padding-left: 2rem;
}

.grid-decks {
  margin-left: 3rem;
  margin-bottom: 3rem;
  width: 100%;
  display: grid;
  grid-template-columns: 25% 20% 50%;
  background-image: linear-gradient(to right, rgba(216, 212, 212, 0.4) 0%, rgba(216, 212, 212, 0.4) 50%, rgba(0, 0, 0, 0.8) 50%, rgba(0, 0, 0, 0.8) 100%);
  border-radius: 3rem;
}

ul li {
  border: 1px solid white;
  padding: 1rem;
}

a {
  text-decoration: none;
  color: white;
}

a:hover {
  text-decoration: underline;
}

.documentation p {
  font-family: "Lato", sans-serif;
  font-size: 1.8rem;
}

.captain-font {
  font-family: captain;
  font-weight: 400;
}

.request {
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid #d9d9d9;
  padding-left: 1rem;
  overflow: hidden;
}

.request pre {
  overflow-x: auto;
}

.list {
  list-style: square;
  padding-left: 2.5rem;
}

.list li {
  border: unset;
  padding-inline: 0;
}

.list span {
  background-color: rgba(248, 239, 239, 0.6);
  font-weight: 700;
  padding-inline: 1rem;
}

.doc-grid {
  display: grid;
  grid-template-columns: 20% 75%;
  column-gap: 4rem;
}

.doc-grid .min-20 {
  position: fixed;
}

.doc-grid .documentation {
  grid-column: 2/3;
}

h1, h3 {
  scroll-margin-top: 9rem;
}

.attribute {
  font-weight: 700;
}